import { useState } from 'react';
import Column from './components/Column';
import CreateTask from './components/CreateTask';
import FooterNav from './components/FooterNav';

const taskStatuses = [
  ' ',
  ' ',
  ' ',
  ' ',
];

const initialState = [
  { id: 1, title: '', status: ' ', priority: 1 },
  { id: 2, title: ' ', status: ' ', priority: 2 },
  { id: 3, title: ' ', status: ' ', priority: 3 },
  { id: 4, title: '', status: ' ', priority: 4 },
];

function App() {
  const [tasks, setTasks] = useState(initialState);

  const onStateChange = (id, newStatus) => {
    setTasks(
      tasks.map((task) =>
        task.id === id ? { ...task, status: newStatus } : task
      )
    );
  };

  const onCreateTasks = (task) => {
    setTasks([...tasks, { ...task, id: Date.now(), status: ' ' }]);
  };

  return (
    <div className="container">
      <CreateTask onSubmit={onCreateTasks} />
      <div className="row" style={{ textAlign: 'center' }}>
        {taskStatuses.map((status) => (
          <Column
            key={status}
            status={status}
            tasks={tasks}
            statuses={taskStatuses}
            onStateChange={onStateChange}
          />
        ))}
      </div>
      <div
        style={{
          justifyContent: 'center',
          alignItems: 'center',
          display: 'flex',
          marginTop: '10rem',
         
        }}
      >
        <FooterNav />
      </div>
    </div>
  );
}

export default App;
